import { Figther } from "./fighter";

/**
 * Clase que representa a los personajes del universo Pokemon
 */
export class Pokemon extends Figther {
  constructor(public nombre: string, public peso_altura: string[], protected universo: string, protected ataque: number, 
    protected defensa: number, protected velocidad: number, protected hit_points: number, protected frase_estelar: string, protected tipo: string) {
      super(nombre, peso_altura, universo, ataque, defensa, velocidad, hit_points, frase_estelar, tipo);
    }

    /**
     * Metodo que retorna el universo al que pertenece el luchador
     * @returns Universo al que pertenece el luchador
     */
     getUniverso() {
      return this.universo;
    }

    /**
     * Metodo que retorna el ataque del luchador
     * @returns Ataque del luchador
     */
    getAtaque() {
      return this.ataque;
    }

    /**
     * Metodo que retorna la defensa del luchador
     * @returns Defensa del luchador
     */
    getDefensa() {
      return this.defensa;
    }

    /**
     * Metodo que retorna la velocidad del luchador
     * @returns Velocidad del luchador
     */
    getVelocidad() {
      return this.velocidad;
    }

    /**
     * Metodo que retorna el HP del luchador
     * @returns HP del luchador
     */
    getHP() {
      return this.hit_points;
    }

    /**
     * Metodo que retorna la frase estrella del luchador
     * @returns Frase estrella del luchador
     */
    getFraseEstelar() {
      return this.frase_estelar;
    }

    /**
     * Metodo que retorna el tipo del pokemon 
     * @returns Tipo del pokemon
     */
    getTipo() {
      return this.tipo;
    }
}